import Link from 'next/link';
import {
  Card,
  CardAction,
  CardContent,
  CardDescription,
  CardFooter,
  CardHeader,
  CardTitle,
} from "@/components/ui/card";

const ProductsList = ({productsData}) => {
  return (
    <div>
      <h1 className='text-6xl text-center mt-24'>Products</h1>
      <Link href={'/'}>
        <h1 className="text-center underline">Home</h1>
      </Link>
      <div className="w-10/12 mx-auto pt-12 grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-4">
        {productsData?.length > 0 && productsData?.map((product: any, id: number) => (
          <Link key={product.id} href={`/products/${product.id}`} className="no-underline" />
        ))}
      </div>
    </div>
  )
}

export default ProductsList